import React from 'react';
import ClassInfo from './ClassInfo/ClassInfo';
import '../bootstrap.css';
import './CharacterData.css';

class CharacterData extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      id: localStorage.getItem('characterId'),
      results: this.props.results,
    }
  }

  static getDerivedStateFromProps(newProps, oldState) {
    if (newProps.results === oldState.results) {
      return null;
    }
    return {
      results: newProps.results,
    };
  }

  render(){
    if (!this.state.results || this.state.results.Error === true) {
      return null
    } else {
  return (
    <>
    <div class="card mb-3">
  <div class="card-body">
    <h5 class="card-title">{this.state.results.Character.Name}</h5>
    <h6 class="card-subtitle text-muted">{this.state.results.FreeCompany.Name}</h6>
  </div>
  <img height="96px" width="96x" src={this.state.results.Character.Avatar} alt="Character Avatar" />
  <div class="card-body">
    <p class="card-text">Level {this.state.results.Character.ActiveClassJob.Level} {this.state.results.Character.ActiveClassJob.Name}
    <br/> {this.state.results.Character.ActiveClassJob.ExpLevel}/{this.state.results.Character.ActiveClassJob.ExpLevelMax} experience
    </p>
  </div>
  <ClassInfo classes={this.state.results.Character.ClassJobs} />
</div>
</>
  );
}}
}

export default CharacterData;
